import React, { useState } from "react";
import api from "../api";
import { useNavigate } from "react-router-dom";
import { motion } from "framer-motion";

export default function Signup() {
  const [form, setForm] = useState({
    name: "",
    lrn: "",
    email: "",
    password: "",
    grade_level_strand: "",
    contact_number: "",
  });
  const nav = useNavigate();

  async function submit(e) {
    e.preventDefault();
    try {
      const { data } = await api.post("/auth/register", form);
      localStorage.setItem("token", data.token);
      nav("/dashboard");
      window.location.reload();
    } catch (err) {
      alert(err?.response?.data?.error || "Sign up failed");
    }
  }

  return (
    <div
      className="fixed inset-0 flex items-center justify-center w-full h-full overflow-hidden"
      style={{
        backgroundImage:
          "url(/schooBg.jpg)",
        backgroundSize: "cover",
        backgroundPosition: "center",
      }}
    >
      {/* Floating gradient background glow */}
      <motion.div
        className="absolute inset-0 bg-gradient-to-br from-blue-500/30 via-purple-500/20 to-transparent blur-3xl"
        animate={{ opacity: [0.6, 0.9, 0.6], scale: [1, 1.05, 1] }}
        transition={{ duration: 8, repeat: Infinity, ease: "easeInOut" }}
      />

      {/* Signup form card */}
      <motion.form
        onSubmit={submit}
        initial={{ opacity: 0, y: 40 }}
        animate={{ opacity: 1, y: 0 }}
        transition={{ duration: 0.8, ease: "easeOut" }}
        className="relative w-full max-w-md sm:max-w-lg bg-white/20 rounded-2xl shadow-2xl
                   px-4 sm:px-8 py-6 sm:py-10 flex flex-col items-center z-10 backdrop-blur-xl border border-white/30 max-h-[90vh] overflow-y-auto"
      >
        <motion.div
          className="w-14 h-14 sm:w-16 sm:h-16 rounded-full bg-gradient-to-br from-blue-500 to-purple-500 flex items-center justify-center mb-3 sm:mb-4 shadow-lg"
          initial={{ scale: 0.8, opacity: 0 }}
          animate={{ scale: 1, opacity: 1 }}
          transition={{ delay: 0.3, duration: 0.6, type: "spring" }}
        >
          <img
            src="/schoolLogo.jpg"
            alt="Logo"
            className="w-8 h-8 sm:w-full sm:h-full rounded-full"
          />
        </motion.div>

        <h2 className="text-2xl sm:text-3xl font-extrabold text-white mb-1 sm:mb-2 text-center">
          Sign Up
        </h2>
        <p className="text-gray-200 mb-4 sm:mb-6 text-center text-sm sm:text-base">
          Create your BCSHS Library account
        </p>

        {[
          { placeholder: "Full Name", key: "name" },
          { placeholder: "LRN", key: "lrn" },
          { placeholder: "Email", key: "email" },
          { placeholder: "Password", key: "password", type: "password" },
          { placeholder: "Grade Level - Strand", key: "grade_level_strand" },
          { placeholder: "Contact Number", key: "contact_number" },
        ].map((field) => (
          <motion.input
            key={field.key}
            placeholder={field.placeholder}
            type={field.type || "text"}
            value={form[field.key]}
            onChange={(e) => setForm({ ...form, [field.key]: e.target.value })}
            whileFocus={{
              scale: 1.03,
              boxShadow: "0 0 20px rgba(59,130,246,0.6)",
            }}
            className="rounded-xl px-4 py-2 sm:px-5 sm:py-3 bg-white/70 text-gray-800 border-none shadow 
                       focus:outline-none transition w-full mb-2 sm:mb-4 text-sm sm:text-base"
          />
        ))}

        <motion.button
          whileHover={{ scale: 1.05, boxShadow: "0 0 25px rgba(59,130,246,0.8)" }}
          whileTap={{ scale: 0.97 }}
          className="rounded-xl bg-gradient-to-r from-blue-500 to-purple-500 text-white font-bold py-2 sm:py-3 mt-2 shadow-lg transition w-full text-sm sm:text-base"
        >
          Sign Up
        </motion.button>

        <div className="mt-3 sm:mt-5 text-gray-200 text-xs sm:text-sm text-center">
          <span>Already have an account? </span>
          <a href="/login" className="text-blue-300 font-bold hover:underline">
            Login
          </a>
        </div>
      </motion.form>
    </div>
  );
}